let navLinks = document.querySelectorAll('.main-header__nav-link');

function btnClick() {
	[].forEach.call(navLinks, (el) => {
		if (el !== this) {
			el.classList.remove('active');
		}
	});

	this.classList.toggle('active');
}

[].forEach.call(navLinks, (el) => {
	el.addEventListener('click', btnClick, false);
});

let menuBtn = document.querySelector('.js-burger-btn');

let menuBurger = menuBtn.querySelector('.main-header__burger');

let topHeaderNavList = document.querySelector('.js-main-header-nav-list');

menuBtn.addEventListener('click', () => {
	if (menuBurger.classList.contains('active')) {
		menuBurger.classList.remove('active');
		topHeaderNavList.classList.remove('active');
	} else {
		menuBurger.classList.add('active');
		topHeaderNavList.classList.add('active');
	}
});
